.Nav{
    list-style: none;
    display: -ms-flexbox;
    display: flex;
    direction: rtl;
    -ms-flex-line-pack: center;
        align-content: center;
    padding: 5px;
}

.Nav a{
    margin: 5px;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
    color: rgb(78, 78, 78)
}

.Nav a:hover{
    color: grey;
}


